body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Item{
  display: -webkit-flex;
  display: flex;
}

.Image{
  width: 30%;
  height: 30%;
}

.New-price{
  font-weight: bold;
}

.Qty{
  float: right;
}

.Old-price{
  color: gray;
  font-weight: bold;
  text-decoration: line-through;
}
.Item-detail{
  margin: 1rem 0;
}

.Toggle-item{
  border: 1px solid transparent;
  margin: 1rem 0;
  width: 70%;
}
.Toggle-item:hover{
  border: 1px solid gray;
  cursor: pointer;
}

.Text{
  text-decoration: underline;
}

.Sign{
  font-size: 1rem;
  margin-left: 7px;
}

.Hidden{
  display: none;
}

.Display{
  display: block;
}

.Promo{
  margin: 1rem 0;
}

.Toggle-promo{
  border: 1px solid transparent;
  margin: 1rem 0;
  width: 80%;
}
.Toggle-promo:hover{
  border: 1px solid gray;
  cursor: pointer;
}

.Text{
  text-decoration: underline;
}

.Sign{
  font-size: 1rem;
  margin-left: 7px;
}

.Promo-text{
  color: gray;
  font-weight: bold;
}

.Code{
  display: inline-block;
  width: 60%;
  height: 2rem;
  border: 1px solid #ddd;
  border-radius: 3px;
  margin: 8px 0;
  font-size: 1rem;
  box-sizing: border-box;
}

.Button{
  display: inline-block;
  width: 30%;
  height: 2rem;
  border-radius: 10px;
  border: 2px solid black;
  font-size: 1rem;
  margin-left: 1rem;
}

.Hidden{
  display: none;
}

.Display{
  display: block;
}
.App {
  width: 200px;
  margin: 0 auto;
  text-align: left;
  padding: 10px;
  border: 1px solid gray;
}

.Loading{
  width: 30px;
  margin: 0 auto;
}

.Prices{
  margin: 0.5rem 0 1rem 0;
}

.Dollors{
  float: right;
  font-weight: bold;
}

.Applied{
  color: dodgerblue;
  font-size: 12px;
}

.Total{
  font-weight: bolder;
  font-size: 18px;
  margin-bottom: 0.5rem;
}

.Tooltip{
  text-decoration: underline;
  display: inline-block;
  position: relative;
  border: 1px soild transparent;
}

.Tooltip-text{
  visibility: hidden;
  width: 200%;
  background:white;
  border: 1px solid #ddd;
  border-radius: 5px;
  text-align: left;
  padding: 10px;
  position: absolute;
  z-index: 1;
  top: 180%;
  right: -50%;
  margin-right: -10px;
}

.Tooltip:hover .Tooltip-text{
  visibility: visible;
}

.Tooltip-text::after {
  content: " ";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -10px;
  border: 10px solid transparent;
  border-bottom-color: white;
}

.Tooltip-text::before {
  content: " ";
  position: absolute;
  bottom: 100%;
  left: 50%;
  margin-left: -12px;
  border: 12px solid transparent;
  border-bottom-color: #ddd;
}

@media screen and (max-width) {
  .App {
    width: 100%;
  }
  .Dollors{
    display: block;
    float: none;
  }

  .Tooltip-text{
    width: 100%;
    left: 0;
  }
}

